.slider__wrapper {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1366px;
  margin: 0 auto;
}

.slider__inner {
  position: relative;
  display: flex;
  justify-content: center;
  width: 1164px;

  @include tablet {
    width: 567px;
  }

  @include mobile {
    width: 226px;
  }
}

.slider__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.slider__item {
  position: relative;

  @include tablet {
    width: 268px;
  }


  @mixin vp-1280 {
  @media (min-width: $vp-1200) {
      &:hover {
    .coaches-card {
      visibility: visible;
      opacity: 1;
    }

    p {
      display: none;
    }
  }
  }
}



  p {
    width: 100%;
    position: absolute;
    bottom: 22px;
    left: 50%;
    transform: translateX(-50%);
    margin: 0;
    text-align: center;
    font-family: $ff-roboto-bold;
    font-weight: 700;
    font-size: 18px;
    line-height: 27px;
    color: $color-default-white;
  }
}

.slider__picture {
  display: block;
}

.slider__image {
  width: 100%;
}

.slider__button-wrapper {
  position: relative;
  width: calc(103 / 1366 * 100%);
  height: 100px;

  @include mobile {
    width: 11%;
  }
}

.slider__button {
  top: 16%;
  min-width: 90%;
  height: 100%;
  border: none;
  background-color: $color-default-white;
  transform: scale(0.55);

  &--prev {
    left: 0;

    @include tablet {
      left: 24px;
    }

    @include mobile {
      left: 12%;
    }
  }

  &--next {
    right: 0;

    @include tablet {
      left: -24px;
    }

    @include mobile {
      left: 0;
    }
  }
}
